.modal {
    animation: modal-in 0.5s cubic-bezier(0.38, 0.7, 0.125, 1) forwards;
    background-color: var(--bg2);
    display: grid;
    left: 0;
    min-height: 180px;
    padding: 10px;
    position: fixed;
    right: 0;
    row-gap: 12px;
    top: 0;
    touch-action: none;
    z-index: 999;

    @media (min-width: 670px) {
        border-radius: 0 0 12px 12px;
        margin: 0 auto;
        max-width: 670px;
    }
}

@keyframes modal-in {
    from {
        transform: translateY(-100%);
    }

    to {
        transform: translateY(0);
    }
}

.inputField {
    background-color: var(--bg4);
    border-radius: 7px;
    caret-color: transparent;
    font-size: 18px;
    margin: 0;
    padding: 0;
    padding: 10px;
    resize: none;
    user-select: auto;
}

.actionButtons {
    align-items: center;
    display: grid;
    gap: 7px;
    grid-template-columns: 1fr 1fr 1fr;

    button {
        align-items: center;
        background: var(--bg4);
        border-radius: 7px;
        display: flex;
        font-size: 13px;
        height: 44px;
        justify-content: center;
    }
}
